@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
html {
  font-size: 62.5%; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif; }
  @media screen and (max-width: 768px) {
    body p {
      font-size: 2.5rem; }
    body h1 {
      font-size: 4rem; }
    body h2 {
      font-size: 3rem; } }
  body p {
    font-size: 2rem; }
  body h1 {
    font-size: 4rem; }
  body h2 {
    font-size: 3rem; }

header {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center; }
  header p {
    margin-top: 0; }
  header .aboutMe {
    border-bottom: 2px solid gray; }
    header .aboutMe:hover {
      -webkit-filter: drop-shadow(0 0 5px gray);
              filter: drop-shadow(0 0 5px gray);
      cursor: pointer; }
  header .line {
    height: 2px;
    width: 0%;
    background-color: black;
    -webkit-transition: width 1s;
    transition: width 1s; }
    header .line.animate {
      width: 85%; }
      @media screen and (max-width: 768px) {
        header .line.animate {
          width: 95%; } }
  header .aboutMeSection {
    visibility: hidden;
    height: 0;
    width: calc(85% - 4px);
    border: 2px solid black;
    border-top: 0;
    -webkit-transition: height 1s, visibility 1s;
    transition: height 1s, visibility 1s;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center; }
    @media screen and (max-width: 768px) {
      header .aboutMeSection {
        width: calc(95% - 4px); } }
    header .aboutMeSection p {
      margin: 10px 0 10px 0; }
    header .aboutMeSection > p {
      -webkit-transition: -webkit-filter .1s;
      transition: -webkit-filter .1s;
      transition: filter .1s;
      transition: filter .1s, -webkit-filter .1s;
      -webkit-filter: opacity(0%);
              filter: opacity(0%); }
    header .aboutMeSection.expanded {
      height: 300px;
      visibility: visible; }
      @media screen and (max-width: 768px) {
        header .aboutMeSection.expanded {
          height: 200px; } }
      header .aboutMeSection.expanded > p {
        -webkit-transition-delay: .5s;
                transition-delay: .5s;
        -webkit-filter: opacity(100%);
                filter: opacity(100%); }

.projects {
  display: flex;
  margin-bottom: 30px;
  flex-wrap: wrap;
  justify-content: space-evenly; }

.project {
  width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px 20px 10px;
  border: 1px solid gray;
  overflow: hidden; }
  @media screen and (max-width: 768px) {
    .project {
      width: 95%; } }
  .project h2 {
    margin: 5px; }
  .project .description {
    margin-top: 0 5px 5px 5px; }
  .project > img {
    background-position: center;
    background-size: cover;
    width: 100%;
    object-fit: cover;
    height: 125px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    border-bottom: 1px solid gray; }
  .project:hover > img {
    -webkit-filter: grayscale(0);
            filter: grayscale(0);
    -webkit-transform: scale(1.05);
            transform: scale(1.05); }
  .project .links {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 20px; }
    .project .links img {
      height: 40px;
      border-bottom: 2px solid black;
      -webkit-transition: -webkit-filter .1s;
      transition: -webkit-filter .1s;
      transition: filter .1s;
      transition: filter .1s, -webkit-filter .1s; }
      .project .links img:hover {
        -webkit-filter: drop-shadow(0 0 5px gray);
                filter: drop-shadow(0 0 5px gray); }

footer {
  position: fixed;
  bottom: -30px;
  height: 30px;
  width: 100%;
  border-top: 1px solid gray;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  -webkit-transition: all .5s;
  transition: all .5s; }
  @media screen and (max-width: 768px) {
    footer {
      bottom: -50px;
      height: 50px; } }
  footer.animate {
    bottom: 0; }
  footer p {
    margin: 5px; }
  footer a {
    text-decoration: none;
    border-bottom: 1px solid gray;
    -webkit-transition: all .3s;
    transition: all .3s;
    color: black; }
    footer a:hover {
      border-bottom: 5px solid black;
      -webkit-filter: drop-shadow(0 0 5px black);
              filter: drop-shadow(0 0 5px black); }

